@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;700&display=swap);
html {
    scrollbar-width: none;
}

body {
    min-width: 1020px;
    overflow: scroll;
    overflow-x: hidden;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar {
    display: none;
}

.top-bar {
    display: flex;
    height: 65px;
    flex-direction: row;
    align-items: center;
    padding: 0 23%;
    justify-content: space-between;
}

.top-bar-item {
    display: flex;
    height: 65px;
    flex-direction: row;
    align-items: center;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 16px;
    color: black;
    font-weight: normal;
}

.top-bar-item div {
    margin: 0 30px;
}

.main-body {
    position: relative;
    display: flex;
    flex-direction: row;
    z-index: 1;
}

.main-body-item {
    display: flex;
    width: 50%;
    justify-content: center;
    padding-top: 50px;
    align-items: baseline;
}

.floating-text {
    font-family: 'IBM Plex Sans', sans-serif;
    position: absolute;
    z-index: 10;
    left: 23%;
    top: 100px;
}

.floating-text-title {
    font-size: 48px;
    color: #057EFF;
    font-weight: bold;
    margin: 30px 0;
}

.floating-text-small {
    font-size: 18px;
    color: #525252;
    margin-bottom: 40px;
}

.web-login-forgot-password {
    font-size: 14px;
    text-decoration: underline;
    color: #BEBEBE;
}

.web-login-lower-controls {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: center;
}

.web-login-remember-me {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 10px;
    font-size: 14px;
}

.web-entry-main {
    display: flex;
    flex-direction: column;
}

.web-entry-main-body {
    font-family: "IBM Plex Sans", serif;
    position: relative;
    margin: 100px auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.web-entry-body-divider {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
}

.web-entry-body-divider-bar {
    background-color: #BEBEBE;
    width: 100%;
    height: 2px;
}

.web-entry-body-divider-text {
    padding: 0 8px;
    color: #BEBEBE;
    vertical-align: center;
}

.web-entry-change-page {
    display: flex;
    flex-direction: row;
    align-content: center;
}

.web-entry-other-options {
    font-weight: bold;
    margin: 10px 0;
    font-size: 16px;
    border-radius: 4px;
}

.web-entry-error {
    display: flex;
    align-self: flex-start;
    align-items: center;
    margin-left: 10px;
    font-size: 12px;
}

.web-topbar {
    position: relative;
    height: 65px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

.web-topbar-divider {
    height: 2px;
    width: 100%;
    bottom: 0;
    position: absolute;
    background-color: #057EFF;
}

.web-signup-strength-text {
    display: flex;
    flex-direction: row;
    color: #BEBEBE;
    font-size: 12px;
    margin: 10px 0 4px 10px;
    align-self: flex-start;
    justify-content: center;
    line-height: 0.8;
}

.web-signup-strength-warning:hover {
    cursor: pointer;
}

.web-signup-strength-notices {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    align-self: flex-start;
    margin-left: 10px;
    font-size: 12px;
}

.web-signup-strength-notices-col {
    display: flex;
    flex-direction: column;
}

.web-signup-strength-notices-col div {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin: 3px;
}

.web-signup-strength-notices-col div svg {
    margin-right: 3px;
    height: 12px;
    width: 12px;
}

.web-strength-bar-main {
    display: flex;
    flex-direction: row;
    align-content: center;
    height: 4px;
    width: 100%;
}

.web-strength-bar-sub {
    margin: 0 3px;
    border-radius: 15px;
    width: 25%;
}

.web-strength-bar-margins {
    width: 10px;
}

.web-account-main {
    display: flex;
    flex-direction: row;
    height: 100%;
}

.web-account-sidebar {
    height: 100%;
    width: 150px;
    background-color: #1E4EE5;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.web-account-logo {
    height: 30px;
    margin: 20px 20px;
    align-self: flex-start;
}

.web-account-main-body {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.web-account-topbar {
    display: flex;
    flex-direction: row;
    background-color: white;
    box-shadow: 0 3px 6px #00000029;
    height: 70px;
    padding: 0 65px;
}

